.container{
  display: flex;
  background-color: #eee;
  .nav-left{
    width:15%;
    height:calc(100vh);
    background-color: #001529;
    position: fixed;
  }
  .main{
    margin-left: 15%;
    flex:1;
    min-height:calc(100vh);
    display: flex;
    flex-direction: column;
    .content{
      position: relative;
      flex:1;
      height:calc(100vh-200px);
      margin:20px 20px 0 20px;
      background-color: #eee;
    }
  }
}


.buttons Button{margin-left:10px;font-size: 14px;letter-spacing: 3px;}
.buttons Card{margin-bottom:0px;}
.card-wrap{margin-bottom:10px;}
.card-wrap Button{margin-left:10px;font-size: 14px;letter-spacing: 3px;}
.card-warp Spin{margin:10px;}
.vertical-cen{text-align:center;top:20%;}
.notifica Button{margin: 0 10px;}
