.header{
  background-color: #fff;
}
.header-top{
  height: 60px;
  line-height: 60px;
  padding: 0 20px;
  text-align: right;
  font-size: 14px;
  .login-name{
    padding: 0 24px;
  }
  a{color:#f9c700;}
}
.header-weather{
  height: 40px;
  line-height: 40px;
  border-top: 1px #f9c700 solid;
  .header-index{
    text-align: center;
  }
  .header-index{
    position: relative;
    font-size: 16px;
  }
  .header-index:after{
    content:"";
    border-left:11px transparent solid;
    border-top:8px #fff solid;
    border-right:11px transparent solid;
    position: absolute;
    top:39px;
    left:40%;
    z-index:10;
  }
  .weather{
    text-align: right;
    .weather-dtail{
      padding:0 20px;
    }
    .date{

    }
  }
}
